<template>
	<view class='search-blog-container'>
		<text></text>
		<!-- 搜索模块 -->
		<view class="search-bar-box">
			<my-search 
			:isShowInput='true' 
			:value='searchText' 
			:config='{"bgColor":"#f1f0f3"}' 
			placeholder="uni-app"
			@confirm='confirm'
			@input='input'
			@cancel='cancel'
			@clear='clear'
			@focus='focus'
			@blur='blur'
		/>
		</view>
		<!-- 热搜模块 -->
		<!-- 历史记录 -->
		<!-- 搜索结果 -->
	</view>
</template>

<script>
	export default {
		data() {
			return {
				searchText:'',  //文本框的输入
			};
		},
		methods:{
			confirm(value){
				this.$emit('confirm',this.value)
			},
			input(){
				this.$emit('input',this.value)
			},
			cancel(){
				this.$emit('cancel',this.value)
			},
			clear(){
				this.$emit('clear',this.value)
			},
			focus(){
				this.$emit('focus',this.value)
			},
			blur(){
				this.$emit('blur',this.value)
			}
		}
	}
</script>

<style lang="scss" scoped>
.search-blog-container{
	.search-bar-box{
		background-color: #fff;
		padding:3px;
		top:0px;
	}
}
</style>
